import moment from 'moment';
import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Alert, Card, CardBody, CardHeader, Col, CustomInput, FormGroup, Row } from 'reactstrap';
import {
  add,
  currentDate,
  DATE_MONTH_YEAR_PATTERN,
  formatDate,
  InputGroupInline,
  InputType,
  isEmpty,
  toDate,
  WindowComponent,
  WindowComponentProps,
  withTooltip,
} from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../../dependencies/empresaMFPDependency';
import { hasGrupoEmpresaConfig } from '../../../../../dependencies/grupoEmpresaConfigDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { Vinculo } from '../../../../../models/dtos/mfp/vinculo';
import { VinculoMovimentacao } from '../../../../../models/dtos/mfp/vinculoMovimentacao';
import { AVISO_PREVIO_INDENIZADO, AVISO_PREVIO_TRABALHADO, PEDIDO_DEMISSAO } from '../../../../../models/enumerated/mfp/customizacaoTermosTipoEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import CustomizacaoTermoIS from '../../../../components/mfp/customizacaoTermoIS/CustomizacaoTermoIS';
import VinculoIS from '../../../../components/mfp/vinculoIS/VinculoIS';
import ReportForm from '../../../../components/report/ReportForm';
import { AvisoPrevioRV, CLASS_KEY, REPORT_EVENT, REPORT_TITLE } from './avisoPrevioRV';

const DEFAULT_VALUES: AvisoPrevioRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  acordo: null,
  tipo: null,
  utilizarDatasRescisao: false,
  vinculo: null,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT),
  exibirConfiguracaoDadosEmpresa: false,
};

const AvisoPrevioReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<AvisoPrevioRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  const [isHiddenDataAvisoPrevio, setIsHiddenDataAvisoPrevio] = useState(false);
  const [isHiddenDuracaoAviso, setIsHiddenDuracaoAviso] = useState(false);
  const [isHiddenPeriodoAvisoInicio, setIsHiddenPeriodoAvisoInicio] = useState(false);
  const [isHiddenPeriodoAvisoFim, setIsHiddenPeriodoAvisoFim] = useState(false);
  const [isHiddenDataPagamento, setIsHiddenDataPagamento] = useState(false);
  const [isHiddenDataAssinatura, setIsHiddenDataAssinatura] = useState(true);
  const [isHiddenUtilizarDatasRescisao, setIsHiddenUtilizarDatasRescisao] = useState(false);
  const [isUtilizarDatasRescisao, setIsUtilizarDatasRescisao] = useState(true);
  const [isDisabled, setIsDisabled] = useState(false);
  const [dataAvisoState, setDataAvisoState] = useState();
  const [lblNaoPossuiRescisao, setLblNaoPossuiRescisao] = useState(true);

  useEffect(
    () => {
      setTimeout(() => {
        formProps.setValue('dataAssinatura', formatDate(currentDate()));
      }, 200);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  useEffect(
    () => {
      const vinculo: Vinculo = formProps.getValues().vinculo;
      if (isEmpty(vinculo)) {
        setIsHiddenUtilizarDatasRescisao(false);
      } else {
        if (isEmpty(vinculo.recisao)) {
          setLblNaoPossuiRescisao(false);
          setIsUtilizarDatasRescisao(false);
          setIsHiddenUtilizarDatasRescisao(false);
          setIsDisabled(false);
        } else {
          setLblNaoPossuiRescisao(true);
          setIsHiddenUtilizarDatasRescisao(true);
          setIsUtilizarDatasRescisao(true);
          setIsDisabled(true);
          setIsHiddenUtilizarDatasRescisao(true);
        }
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [formProps.getValues().vinculo]
  );

  useEffect(
    () => {
      const vinculo: Vinculo = formProps.getValues().vinculo;

      setTimeout(() => {
        if (!isEmpty(vinculo) && !isEmpty(vinculo.recisao)) {
          if (isUtilizarDatasRescisao) {
            ajustarCamposAoSelecionarVinculo(vinculo);
          }
        } else {
          limparCampos();
        }
      }, 200);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [
      isUtilizarDatasRescisao,
      isHiddenDataAvisoPrevio,
      isHiddenDataPagamento,
      isHiddenDuracaoAviso,
      isHiddenPeriodoAvisoInicio,
      isHiddenDataAssinatura,
      isHiddenPeriodoAvisoFim,
      formProps.getValues().vinculo,
    ]
  );

  const limparCampos = () => {
    formProps.setValue('dataAviso', '');
    formProps.setValue('periodoAvisoInicio', '');
    formProps.setValue('duracaoAvisoPrevio', 0);
    formProps.setValue('periodoAvisoFim', '');
    formProps.setValue('dataPagamento', '');
    formProps.setValue('periodoAvisoInicio', '');
    setDataAvisoState('');
  };

  const verificarDatasDoAviso = () => {
    const vinculo: Vinculo = formProps.getValues().vinculo;

    const dataAviso = formProps.getValues().dataAviso;

    if (!isEmpty(vinculo)) {
      if (!isEmpty(formProps.getValues().tipo)) {
        switch (formProps.getValues().tipo.tipo) {
          case AVISO_PREVIO_TRABALHADO.value || AVISO_PREVIO_INDENIZADO.value:
            ajustarDataInicioAviso(toDate(dataAviso));
            ajustarDataFinalAviso(vinculo, toDate(dataAviso));
            break;
        }
      }
    }
  };

  const ajustarCamposAoSelecionarVinculo = (vinculo: Vinculo) => {
    const utilizarDatasRescisao: boolean = formProps.getValues().utilizarDatasRescisao;
    if (utilizarDatasRescisao) {
      preencherCampos(vinculo);
    }
  };

  const preencherCampos = (vinculo: Vinculo): void => {
    if (!isEmpty(vinculo.recisao.dtAvisoPrevio)) {
      formProps.setValue('dataAviso', formatDate(toDate(vinculo.recisao.dtAvisoPrevio)), true);
      formProps.setValue('periodoAvisoInicio', formatDate(add(toDate(vinculo.recisao.dtAvisoPrevio), 1, 'day')), true);
      formProps.setValue('periodoAvisoFim', formatDate(add(toDate(vinculo.recisao.dtAvisoPrevio), vinculo.recisao.duracaoAviso, 'day')), true);
    }

    if (!isEmpty(vinculo.recisao.duracaoAviso)) {
      formProps.setValue('duracaoAvisoPrevio', Number(vinculo.recisao.duracaoAviso));
    }

    if (!isEmpty(vinculo.recisao.dtPagamento)) {
      formProps.setValue('dataPagamento', vinculo.recisao.dtPagamento, true);
    }
  };

  const ajustarDataInicioAviso = (dataAviso: Date): void => {
    formProps.setValue('periodoAvisoInicio', formatDate(add(dataAviso, 1, 'day')), true);
  };

  const ajustarDataFinalAviso = (vinculo: Vinculo, dataAviso: Date) => {
    const periodoDiasPadrao: number = 30;
    const periodoAvisoAdicional: number = 3;
    let anosTrabalhados: number;
    let diasAcrescentar: number = periodoDiasPadrao;

    const dataAdmissao = moment(vinculo.dataAdmissao, DATE_MONTH_YEAR_PATTERN);

    const daysBetween: number = moment(dataAviso, DATE_MONTH_YEAR_PATTERN).diff(dataAdmissao, 'days');

    anosTrabalhados = Math.floor(daysBetween / 365);

    if (anosTrabalhados >= 1) {
      diasAcrescentar += periodoAvisoAdicional * anosTrabalhados;

      diasAcrescentar = diasAcrescentar > 90 ? 90 : diasAcrescentar;
    }

    formProps.setValue('periodoAvisoFim', formatDate(add(dataAviso, diasAcrescentar, 'day')), true);
  };

  useEffect(
    () => {
      if (!isEmpty(formProps.getValues().tipo)) {
        switch (formProps.getValues().tipo.tipo) {
          case AVISO_PREVIO_TRABALHADO.value:
            setIsHiddenDataPagamento(false);
            setIsHiddenDataAssinatura(true);
            setIsHiddenDataAvisoPrevio(true);
            setIsHiddenPeriodoAvisoInicio(true);
            setIsHiddenPeriodoAvisoFim(true);
            setIsHiddenDuracaoAviso(false);
            break;
          case AVISO_PREVIO_INDENIZADO.value:
            setIsHiddenDataAvisoPrevio(false);
            setIsHiddenPeriodoAvisoInicio(false);
            setIsHiddenPeriodoAvisoFim(false);
            setIsHiddenDataAssinatura(true);
            setIsHiddenDuracaoAviso(false);
            setIsHiddenDataPagamento(true);
            break;
          case PEDIDO_DEMISSAO.value:
            setIsHiddenDuracaoAviso(true);
            setIsHiddenPeriodoAvisoInicio(true);
            setIsHiddenPeriodoAvisoFim(true);
            setIsHiddenDataAssinatura(true);
            setIsHiddenDataAvisoPrevio(false);
            setIsHiddenDataPagamento(false);
            break;
        }
      } else {
        setIsHiddenDataAvisoPrevio(false);
        setIsHiddenDuracaoAviso(false);
        setIsHiddenPeriodoAvisoInicio(false);
        setIsHiddenPeriodoAvisoFim(false);
        setIsHiddenDataPagamento(false);
        setIsHiddenDataAssinatura(true);
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [formProps.getValues().tipo]
  );

  const changeUtilizarDatasRescisao = () => {
    const vinculo: Vinculo = formProps.getValues().vinculo;

    if (!isEmpty(vinculo) && !isEmpty(vinculo.recisao)) {
      if (isUtilizarDatasRescisao) {
        setIsUtilizarDatasRescisao(false);
        setIsDisabled(false);
        limparCampos();
      } else {
        setIsUtilizarDatasRescisao(true);
        setIsDisabled(true);
      }
    }
  };

  const containTagNoTermoOuAcordo = (tag: string): boolean => {
    if (!isEmpty(formProps.getValues().tipo) && formProps.getValues().tipo.conteudo.includes(tag)) {
      return true;
    }
  };

  const validateExtras = (): string[] => {
    const avisoPrevioRV: AvisoPrevioRV = formProps.getValues();
    const erros = [];

    if (!isEmpty(avisoPrevioRV.tipo)) {
      if (!avisoPrevioRV.utilizarDatasRescisao) {
        if (isEmpty(avisoPrevioRV.periodoAvisoInicio) && containTagNoTermoOuAcordo('<DATA_INICIAL>')) {
          erros.push('Per√≠odo de Aviso In√≠cio - Campo n√£o pode estar em branco.');
        }
        if (isEmpty(avisoPrevioRV.periodoAvisoFim) && containTagNoTermoOuAcordo('<DATA_FINAL>')) {
          erros.push('Per√≠odo de Aviso Fim - Campo n√£o pode estar em branco.');
        }
        if (isEmpty(avisoPrevioRV.dataPagamento) && containTagNoTermoOuAcordo('<DATA_PAGAMENTO>')) {
          erros.push('Data do Pagamento - Campo n√£o pode estar em branco.');
        }

        if (isEmpty(avisoPrevioRV.dataAviso) && containTagNoTermoOuAcordo('<DATA_AVISO>')) {
          erros.push('Data de Aviso - Campo n√£o pode estar em branco.');
        }

        if (isEmpty(avisoPrevioRV.duracaoAvisoPrevio) && containTagNoTermoOuAcordo('<QUANTIDADE_DIAS_AVISO_PREVIO>')) {
          erros.push('Dura√ß√£o do Aviso Pr√©vio - Campo n√£o pode estar em branco.');
        }

        if (isEmpty(avisoPrevioRV.dataComparecimento) && containTagNoTermoOuAcordo('<DATA_COMPARECIMENTO>')) {
          erros.push('Data do Comparecimento - Campo n√£o pode estar em branco.');
        }

        if (isEmpty(avisoPrevioRV.horaComparecimento) && containTagNoTermoOuAcordo('<HORA_COMPARECIMENTO>')) {
          erros.push('Hora do Comparecimento - Campo n√£o pode estar em branco.');
        }

        if (formProps.getValues().tipo.tipo === AVISO_PREVIO_TRABALHADO.value) {
          const avisoPrevioTrabalhado: boolean = true;
          if (isEmpty(avisoPrevioRV.dataAviso)) {
            if (avisoPrevioTrabalhado) {
              erros.push('Data do Aviso Pr√©vio - Campo n√£o pode estar em branco.');
            }
            if (containTagNoTermoOuAcordo('<DATA_AVISO>')) {
              erros.push('Data do Aviso Pr√©vio - Campo n√£o pode estar em branco.');
            }
          }
        }
      }
    }

    // Validando se a data in√≠cio √© anterior √† data fim.
    if (moment(avisoPrevioRV.periodoAvisoInicio, DATE_MONTH_YEAR_PATTERN).isAfter(moment(avisoPrevioRV.periodoAvisoFim, DATE_MONTH_YEAR_PATTERN))) {
      erros.push('A Data Fim deve ser posterior √† Data Inicio.');
    }

    // Validando as datas
    if (
      moment(avisoPrevioRV.periodoAvisoInicio, DATE_MONTH_YEAR_PATTERN).isBefore(moment(avisoPrevioRV.vinculo.dataAdmissao, DATE_MONTH_YEAR_PATTERN))
    ) {
      erros.push('A Data de In√≠cio do Aviso deve ser posterior √† Data Admiss√£o.');
    }

    if (
      moment(avisoPrevioRV.periodoAvisoFim, DATE_MONTH_YEAR_PATTERN).isBefore(moment(avisoPrevioRV.vinculo.dataAdmissao, DATE_MONTH_YEAR_PATTERN))
    ) {
      erros.push('A Data de Fim do Aviso deve ser posterior √† Data Admiss√£o.');
    }

    if (moment(avisoPrevioRV.dataAviso, DATE_MONTH_YEAR_PATTERN).isBefore(moment(avisoPrevioRV.vinculo.dataAdmissao))) {
      erros.push('A Data do Aviso deve ser posterior √† Data Admiss√£o.');
    }

    if (
      !isEmpty(avisoPrevioRV.dataPagamento) &&
      moment(avisoPrevioRV.dataPagamento, DATE_MONTH_YEAR_PATTERN).isBefore(moment(avisoPrevioRV.vinculo.dataAdmissao, DATE_MONTH_YEAR_PATTERN))
    ) {
      erros.push('A Data de Pagamento deve ser posterior √† Data Admiss√£o.');
    }

    if (moment(avisoPrevioRV.periodoAvisoInicio, DATE_MONTH_YEAR_PATTERN).isBefore(moment(avisoPrevioRV.dataAviso, DATE_MONTH_YEAR_PATTERN))) {
      erros.push('A Data do Aviso n√£o pode ser posterior ao in√≠cio do cumprimento do aviso.');
    }

    // Apenas trabalhadores n√£o afastados podem cumprir aviso.
    const vinculoMovimentacoes: VinculoMovimentacao[] = avisoPrevioRV.vinculo.movimentacoes;
    const refStart = moment(globalReducer.globalParameter.empresaMFP.referencia, 'YYYY-MM');
    refStart.startOf('month');
    const vinculoMovimentacaoAfastamento = vinculoMovimentacoes.map((vinculoMovimentacao: VinculoMovimentacao) => {
      if (isEmpty(vinculoMovimentacao.dataRetorno) || formatDate(toDate(vinculoMovimentacao.dataAfastamento)) < formatDate(refStart.toDate())) {
        return vinculoMovimentacao;
      } else {
        return null;
      }
    });

    if (vinculoMovimentacaoAfastamento[0] != null) {
      erros.push('N√£o √© poss√≠vel emitir Aviso Pr√©vio para Trabalhador Afastado.');
    }

    return erros;
  };

  const tooltip = (message: string, componentId: string) => withTooltip({ message, placement: 'top-start' }, componentId);

  return (
    <WindowComponent
      title="Aviso Pr√©vio"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[
        () => hasEmpresa(globalReducer.globalParameter),
        () => hasEmpresaMFP(globalReducer.globalParameter),
        () => hasGrupoEmpresaConfig(globalReducer.globalParameter),
      ]}
      wikiId={2009}
      wikiTitle={'Relat√≥rio - Aviso Pr√©vio'}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
        validateExtras={validateExtras}
      >
        <VinculoIS
          id="vinculo"
          formProps={{ ...formProps, validation: { required: true } }}
          label="Trabalhador"
          placeholder="Selecione o Trabalhador"
          isPrependLabel={true}
        />
        <CustomizacaoTermoIS
          formProps={{ ...formProps, validation: { required: true } }}
          label="Tipo de Aviso Pr√©vio"
          searchViewProps={{ apenasTipo: true }}
          apenasTipo={true}
          placeholder="Selecione o Tipo de Aviso Pr√©vio"
          id="tipo"
          isPrependLabel={true}
          inputSearchProps={{ size: 'xl' }}
        />
        <CustomizacaoTermoIS
          formProps={{ ...formProps, validation: { required: true } }}
          label="Tipo de Acordo"
          searchViewProps={{ apenasAcordo: true }}
          apenasAcordo={true}
          placeholder="Selecione o Tipo de Acordo"
          id="acordo"
          isPrependLabel={true}
          inputSearchProps={{ size: 'xl' }}
        />
        <FormGroup tag={Row}>
          <Col xs={6}>
            <CustomInput
              id="idexibirAcordoEntrePartes"
              innerRef={formProps.register}
              label="Exibir Acordo Entre as Partes"
              name="exibirAcordoEntrePartes"
              type="switch"
            />
            {tooltip(
              'Campo utilizado para o controle de preenchimento do relat√≥rio. √â referente a exibi√ß√£o do tipo de acordo selecionado.',
              'exibirAcordoEntrePartes'
            )}
          </Col>
          {isHiddenUtilizarDatasRescisao && (
            <Col xs={6}>
              <CustomInput
                id="idutilizarDatasRescisao"
                innerRef={formProps.register}
                label="Utilizar campos da Rescis√£o"
                name="utilizarDatasRescisao"
                type="switch"
                checked={isUtilizarDatasRescisao}
                onChange={changeUtilizarDatasRescisao}
              />
              {tooltip(
                'Campo utilizado para o controle de preenchimento do relat√≥rio. √â referente a exibi√ß√£o do tipo de acordo selecionado.',
                'utilizarDatasRescisao'
              )}
            </Col>
          )}
        </FormGroup>
        <Row>
          <Col>
            <Alert color="primary" className="text-center" hidden={lblNaoPossuiRescisao}>
              Aten√ß√£o, o v√≠nculo n√£o possui rescis√£o cadastrada. Os dados informados n√£o ser√£o salvos para serem enviados para os arquivos digitais.
            </Alert>
          </Col>
        </Row>

        <Card style={{ marginBottom: '16px', borderWidth: 'thin' }}>
          <CardHeader className="font-weight-bold bg-light">Tag</CardHeader>
          <CardBody>
            {isHiddenDataAvisoPrevio && (
              <FormGroup tag={Row}>
                <Col xs={6}>
                  <InputGroupInline
                    id="dataAviso"
                    formProps={formProps}
                    isPrependLabel={true}
                    label="Data do Aviso Pr√©vio"
                    type={InputType.DATE}
                    tooltipProps={{ message: 'Campo utilizado para preencher a TAG DATA_AVISO (Se houver). √â referente √† data do aviso pr√©vio.' }}
                    onBlur={verificarDatasDoAviso}
                    disabled={isDisabled}
                    value={dataAvisoState}
                  />
                </Col>
              </FormGroup>
            )}

            <FormGroup tag={Row}>
              {isHiddenDuracaoAviso && (
                <Col xs={6}>
                  <InputGroupInline
                    id="duracaoAvisoPrevio"
                    formProps={{ ...formProps }}
                    isPrependLabel={true}
                    label="Dura√ß√£o do Aviso Pr√©vio"
                    type={InputType.NUMBER}
                    disabled={isDisabled}
                    tooltipProps={{
                      message: 'Campo utilizado para preencher a QUANTIDADE_DIAS_AVISO_PREVIO (Se houver). √â referente √† dura√ß√£o do aviso pr√©vio',
                    }}
                  />
                </Col>
              )}
            </FormGroup>

            <FormGroup tag={Row}>
              {isHiddenPeriodoAvisoInicio && (
                <Col>
                  <InputGroupInline
                    id="periodoAvisoInicio"
                    formProps={formProps}
                    isPrependLabel={true}
                    label="Data Inicial do Aviso"
                    type={InputType.DATE}
                    onChange={ajustarDataInicioAviso}
                    tooltipProps={{
                      message: 'Campo utilizado para preencher a TAG DATA_INICIAL (Se houver). √â referente √† data inicial do aviso pr√©vio.',
                    }}
                    disabled={isDisabled}
                  />
                </Col>
              )}
              {isHiddenPeriodoAvisoFim && (
                <Col>
                  <InputGroupInline
                    id="periodoAvisoFim"
                    formProps={formProps}
                    isPrependLabel={true}
                    label="Data Final do Aviso"
                    type={InputType.DATE}
                    tooltipProps={{
                      message: 'Campo utilizado para preencher a TAG DATA_FINAL (Se houver). √â referente √† data final do aviso pr√©vio.',
                    }}
                    disabled={isDisabled}
                  />
                </Col>
              )}
            </FormGroup>
            {isHiddenDataPagamento && (
              <FormGroup tag={Row}>
                <Col xs={6}>
                  <InputGroupInline
                    id="dataPagamento"
                    formProps={formProps}
                    isPrependLabel={true}
                    label="Data de Pagamento "
                    type={InputType.DATE}
                    tooltipProps={{
                      message:
                        'Campo utilizado para preencher a TAG DATA_PAGAMENTO (Se houver). √â referente √† data de pagamento dos encargos demissionais.',
                    }}
                    disabled={isDisabled}
                  />
                </Col>
              </FormGroup>
            )}
            {isHiddenDataAssinatura && (
              <FormGroup tag={Row}>
                <Col xs={6}>
                  <InputGroupInline
                    id="dataAssinatura"
                    formProps={formProps}
                    isPrependLabel={true}
                    label="Data da Assinatura"
                    type={InputType.DATE}
                    tooltipProps={{
                      message:
                        'Campo utilizado para preencher a data de assinatura do documento, caso n√£o seja preenchido ser√° utilizada a data atual.',
                    }}
                  />
                </Col>
              </FormGroup>
            )}
          </CardBody>
        </Card>
      </ReportForm>
    </WindowComponent>
  );
};

export default AvisoPrevioReportView;
